html, body, #root, .App  {
    width: 100%;
    height: 100%;
    background-color: #282c34;
    cursor: url("./cursor/c.svg"), auto;
}

#route {
    display: flex;
    flex: 1;
    flex-direction: row-reverse;
    width: 100%;
    height: 100%;
    color: white;
}

#route_content {
    display: inline-flex;
    flex: 1;
    flex-direction: column;
    justify-content: center;
    justify-items: center;
    align-items: center;
    align-content: center;
    width: 100px;
}