.bar {
    display: inline-flex;
    width: 50px; 
    flex-direction: column; 
    justify-items: center; 
    align-items: center;
    text-align: center; 
    background-color: rgb(48, 56, 71);
    padding-top: 5px;
}

.box {
    display: inline-flex;
    width: 40px;
    height: 40px;
    background: url(./img/panel_metalDark.png);
    background-clip: content-box;
    background-repeat: no-repeat;
    background-size: contain;

}